import urls from '../../utils/urls.js'
export default {
  data () {
    return {
      content: ''
    }
  },
  onLoad() {
    uni.request({
      url: urls.HTTP + '/index/privacyPolicy',
      method: 'POST',
      success: (res) => {
        // console.log(res)
        this.content = res.data
      }
    })
  }
}
